/* 
 * EFTOURS JQUERY PLUGIN BOILERPLATE
 * This code is a combination of various approaches outlined in 
 * http://coding.smashingmagazine.com/2011/10/11/essential-jquery-plugin-patterns/
 * ("A lightweight start" merged with "A Highly Configurable And Mutable Plugin")
 
 * CUSTOMIZATION: Update any uses of "plugin" or "Plugin" to your plugin's name
 * each line that needs to change is commented with "CHANGE"
 * Be sure to document/comment your code and give example html and example js!

 * USAGE:
 * example html - uses inline data attribute to override options on per-element basis
 * NOTE: You cannot define functions this way, only simple types & Note the inverted comma nesting!

    <div class="myplugin" data-myplugin-options='{"foo":"foo"}'>this is my plugin!</div>

 * example js - saves plugin object as variable so can access again

    var myplugin = $('.myplugin').myplugin({
            "bar":"bar"
        }).data('plugin_myplugin');

    myplugin.publicstuff();

 * Happy plugin authoring!!!
 */


; define('core/ef_plugins/clippy',["jquery", "core/global/helpers", "libs/zero-clipboard"], function (jQuery, helpers, ZeroClipboard) {
    (function($, window, document, undefined) {

        var pluginName = 'clippy',
            defaults = {
                tooltipText: "Link copied to clipboard." // this should be set for each instance and coming from sitecore!
            };

        // The actual plugin constructor
        function Clippy(element, options) {
            // grab the element the plugin was called on
            this.element = element;

            // also save the element as a jquery object
            this.$element = $(element);

            // get per-element inline configuration settings
            this.metadata = this.$element.data('clippy-options');

            // merge all the configs
            this.options = $.extend({}, defaults, options, this.metadata);

            // save the defaults
            this._defaults = defaults;

            // save the plugin name
            this._name = pluginName;

            // call init function
            this.init();

        }

        // Define this sucker
        Clippy.prototype = {
                init: function() {
                    var plugin = this;
                ZeroClipboard.config({ swfPath: "/content/swf/ZeroClipboard.swf" });
                var client = new ZeroClipboard($(".js-clippy"));
                client.on("ready", function () {
                    console.log("Clippy Loaded");
                });
                client.on("beforecopy", function () {
                    console.log("About to Copy");
                });
                client.on('copy', function (event) {
                    event.clipboardData.setData('text/plain', $(".trip-website-url").text());
                    console.log("Copying");
                    plugin.showAlert();
                });
                client.on("error", function (event) {
                    console.log('ZeroClipboard error of type "' + event.name + '": ' + event.message);
                    ZeroClipboard.destroy();
                });
            },

                showAlert: function () {
                    var plugin = this;
                    var anchor = plugin.$element,
                        $div = $('<div class="keystroke-tooltip"></div>');
                    $div.append(plugin.options.tooltipText);
                    $(anchor).after($div);
                    $div.fadeToggle().delay(2000).fadeToggle();
            },

                    // Make a destroy function now, we'll need it at some point
                destroy: function () {
                    var plugin = this;
                    // whatever you need to do to destroy this sucka goes here
                    // remove event handlers, put html back where it was, etc.

                    plugin.$element.attr("title", "");
                    // remove the data
                    plugin.$element.removeData('plugin_' + pluginName);

                    // remove tooltop
                    $('.keystroke-tooltip').destroy();
                },
        };

        // Turn this into a jquery plugin & prevent multiple instantiations
        $.fn[pluginName] = function (options) {
            return this.each(function () {
                if (!$.data(this, 'plugin_' + pluginName)) {
                    $.data(this, 'plugin_' + pluginName,
                        new Clippy(this, options));
                }
            });
        };

    })(jQuery, window, document);
});
